import Link from "next/link";
import { Button } from "@/components/ui/Button";

export function Navbar() {
    return (
        <nav className="fixed top-6 left-0 right-0 z-50 flex justify-center px-4 pointer-events-none">
            <div className="pointer-events-auto flex items-center justify-between gap-12 bg-white/80 backdrop-blur-md border border-slate-200/50 rounded-full shadow-lg shadow-slate-200/20 px-6 py-2">

                {/* Logo */}
                <Link href="/" className="font-semibold text-slate-900 tracking-tight flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-slate-900"></div>
                    SeTutor
                </Link>

                {/* Links */}
                <div className="hidden md:flex items-center gap-6">
                    <Link href="#features" className="text-sm font-medium text-slate-500 hover:text-slate-900 transition-colors">
                        Features
                    </Link>
                    <Link href="#how-it-works" className="text-sm font-medium text-slate-500 hover:text-slate-900 transition-colors">
                        How it works
                    </Link>
                </div>

                {/* Actions */}
                <div className="flex items-center gap-3">
                    <Link href="/login" className="text-sm font-medium text-slate-900 hover:text-slate-700">
                        Log in
                    </Link>
                    <Link href="/login">
                        <Button
                            size="sm"
                            className="rounded-full bg-slate-900 text-white hover:bg-slate-800 h-8 text-xs px-4"
                        >
                            Get Started
                        </Button>
                    </Link>
                </div>
            </div>
        </nav>
    );
}
