import { FileText, Sparkles, BrainCircuit, BarChart3 } from "lucide-react";

export function Features() {
    return (
        <section id="features" className="py-24 bg-white">
            <div className="container px-4 md:px-6 mx-auto">
                <div className="text-center max-w-3xl mx-auto mb-20 space-y-4">
                    <h2 className="text-3xl font-semibold tracking-tight text-slate-900 sm:text-4xl">
                        Everything you need.
                    </h2>
                    <p className="text-lg text-slate-500 font-light">
                        Powerful features designed for focus and retention.
                    </p>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12">
                    {features.map((feature, index) => (
                        <div
                            key={index}
                            className="group flex flex-col items-start space-y-4"
                        >
                            <div className="p-3 rounded-lg bg-slate-50 text-slate-900 group-hover:bg-slate-100 transition-colors duration-300">
                                {feature.icon}
                            </div>
                            <h3 className="text-lg font-semibold text-slate-900">
                                {feature.title}
                            </h3>
                            <p className="text-slate-500 leading-relaxed text-sm">
                                {feature.description}
                            </p>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}

const features = [
    {
        title: "Document Management",
        description:
            "Upload and organize PDF documents in a hierarchical structure for easy access.",
        icon: <FileText className="w-6 h-6 stroke-[1.5]" />,
    },
    {
        title: "AI Analysis",
        description:
            "Advanced algorithms instantly parse and understand your learning materials.",
        icon: <Sparkles className="w-6 h-6 stroke-[1.5]" />,
    },
    {
        title: "Smart Flashcards",
        description:
            "Automatically generated flashcards to reinforce key concepts and definitions.",
        icon: <BrainCircuit className="w-6 h-6 stroke-[1.5]" />,
    },
    {
        title: "Progress Tracking",
        description:
            "Detailed analytics to monitor your study habits and knowledge retention.",
        icon: <BarChart3 className="w-6 h-6 stroke-[1.5]" />,
    },
];
