import Link from "next/link";
import { Button } from "@/components/ui/Button";
import { ArrowRight, CheckCircle2 } from "lucide-react";

export function Hero() {
    return (
        <section className="relative pt-32 pb-20 lg:pt-48 lg:pb-32 overflow-hidden bg-white">
            <div className="container px-4 md:px-6 mx-auto">
                <div className="flex flex-col items-center text-center space-y-10 max-w-4xl mx-auto">

                    {/* Badge */}
                    <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-slate-50 border border-slate-200 text-slate-600 text-xs font-medium animate-fade-in-up">
                        <span className="relative flex h-2 w-2">
                            <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-slate-400 opacity-75"></span>
                            <span className="relative inline-flex rounded-full h-2 w-2 bg-slate-500"></span>
                        </span>
                        AI-Powered Learning Platform
                    </div>

                    {/* Heading */}
                    <h1 className="text-5xl md:text-7xl font-bold tracking-tight text-slate-900 leading-[1.1] animate-fade-in-up delay-100">
                        Master your studies with <br />
                        <span className="text-slate-900">intelligent tools.</span>
                    </h1>

                    {/* Subtext */}
                    <p className="text-xl text-slate-500 max-w-2xl mx-auto leading-relaxed animate-fade-in-up delay-200">
                        Upload documents, generate flashcards, and track progress.
                        Simple, fast, and engineered for retention.
                    </p>

                    {/* CTA Buttons */}
                    <div className="flex flex-col sm:flex-row items-center gap-4 animate-fade-in-up delay-300">
                        <Link href="/login">
                            <Button
                                size="lg"
                                className="rounded-full bg-slate-900 text-white hover:bg-slate-800 h-12 px-8 text-base shadow-lg shadow-slate-200/50"
                            >
                                Get Started
                                <ArrowRight className="ml-2 w-4 h-4" />
                            </Button>
                        </Link>
                        <Link href="#how-it-works">
                            <Button
                                variant="ghost"
                                size="lg"
                                className="rounded-full text-slate-600 hover:text-slate-900 hover:bg-slate-50 h-12 px-8 text-base"
                            >
                                Learn more
                            </Button>
                        </Link>
                    </div>

                    {/* Trust Indicators */}
                    <div className="flex items-center gap-6 pt-8 animate-fade-in-up delay-300">
                        <div className="flex items-center gap-2 text-slate-500 text-sm">
                            <CheckCircle2 className="w-4 h-4" />
                            <span>Free to start</span>
                        </div>
                        <div className="flex items-center gap-2 text-slate-500 text-sm">
                            <CheckCircle2 className="w-4 h-4" />
                            <span>No credit card</span>
                        </div>
                    </div>
                </div>

                {/* Minimalist Abstract Visual */}
                <div className="mt-20 max-w-5xl mx-auto rounded-xl border border-slate-200 bg-slate-50/50 p-2 shadow-2xl shadow-slate-200/50 animate-fade-in-up delay-300">
                    <div className="rounded-lg bg-white overflow-hidden aspect-[16/9] relative flex items-center justify-center">
                        <div className="absolute inset-0 bg-[radial-gradient(#e2e8f0_1px,transparent_1px)] [background-size:16px_16px] [mask-image:radial-gradient(ellipse_50%_50%_at_50%_50%,#000_70%,transparent_100%)]"></div>

                        {/* Abstract UI representation */}
                        <div className="relative z-10 grid grid-cols-2 gap-8 w-3/4 opacity-80">
                            <div className="space-y-4">
                                <div className="h-8 w-32 bg-slate-100 rounded-md"></div>
                                <div className="h-4 w-full bg-slate-50 rounded-md"></div>
                                <div className="h-4 w-5/6 bg-slate-50 rounded-md"></div>
                                <div className="h-32 w-full bg-slate-50 rounded-xl border border-slate-100 shadow-sm"></div>
                            </div>
                            <div className="space-y-4 pt-12">
                                <div className="h-40 w-full bg-slate-900 rounded-xl shadow-xl flex items-center justify-center text-slate-500">
                                    <div className="w-12 h-12 rounded-full border-2 border-slate-700"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}
