import Link from "next/link";
import { Button } from "@/components/ui/Button";

export function CTA() {
    return (
        <section className="py-32 bg-white">
            <div className="container px-4 md:px-6 mx-auto">
                <div className="bg-slate-900 rounded-[2rem] p-12 md:p-24 text-center max-w-5xl mx-auto shadow-2xl relative overflow-hidden">

                    <div className="relative z-10 space-y-8">
                        <h2 className="text-4xl md:text-5xl font-bold tracking-tight text-white">
                            Ready to learn smarter?
                        </h2>
                        <p className="text-lg text-slate-400 max-w-xl mx-auto font-light">
                            Join thousands of students who have transformed their study habits.
                            Start for free today.
                        </p>
                        <div className="flex justify-center pt-4">
                            <Link href="/login">
                                <Button
                                    size="lg"
                                    className="bg-white text-slate-900 hover:bg-slate-100 rounded-full h-14 px-10 text-lg font-medium shadow-xl border-0"
                                >
                                    Get Started
                                </Button>
                            </Link>
                        </div>
                    </div>

                    {/* Subtle Abstract Decor */}
                    <div className="absolute top-0 left-0 w-full h-full opacity-10 pointer-events-none">
                        <div className="absolute -top-24 -left-24 w-96 h-96 bg-white rounded-full blur-[100px]"></div>
                        <div className="absolute -bottom-24 -right-24 w-96 h-96 bg-white rounded-full blur-[100px]"></div>
                    </div>
                </div>
            </div>
        </section>
    );
}
